html,body{width:100%;height:100%}body{background-color:#e2a82b;margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}

.AppH{background-color:white;padding:10px;width:95%;border-radius:30px;margin:0 auto}.MainSectionH{border-radius:30px;background-color:#dedede;display:flex}

.hotel{width:200px;height:200px;margin:10px;border-radius:5%;overflow:hidden;background-color:white}.hotel img{height:60%;width:100%}.hotel .hotel-info{padding:2px;text-align:center;display:flex;flex-direction:column;justify-content:space-around}.hotel .hotel-info .hotel-name{height:20px;line-height:1rem;font-family:'Tajawal', sans-serif;font-size:0.9rem;font-weight:bolder;margin-top:1px;margin-bottom:15px}.hotel .hotel-info .hotel-location-price{height:25px;margin-top:10px;margin-bottom:5px;padding:0 10px 0px 10px;display:flex;justify-content:space-between;font-size:0.8rem}.hotel .hotel-info .hotel-location-price .price-container{width:35px;height:25px;padding:0.4em 0 0.4em 0;align-items:center;text-align:center;border-radius:50px;background-color:#d8ecf3;font-size:0.6rem;font-weight:bold;color:darkcyan}.main-container{display:flex;flex-wrap:wrap;justify-content:center;align-content:flex-start;padding:20px}


.fav-button{cursor:pointer}.fav-button .fav-icon{width:24px}

.header{display:flex;flex-direction:column;align-items:center}.header img{margin-right:15px;margin-bottom:8px;padding:8px;border:solid  #e6e6e6 2px;border-radius:100%}.header .search-field-location{width:300px;margin:50px 0 30px 0;border:none;border-bottom:solid #e6e6e6 2px;text-align:justify;font-size:2rem;outline:none}.header .search-field-location:hover{border-color:#cccccc}.header .price-currency-change{display:flex;align-items:center;margin-bottom:50px}.header .price-bar-currency{margin-right:20px;height:40px;width:80px;padding:2px 5px 7px 5px;display:flex;align-items:flex-end;border:solid #e6e6e6 2px;border-radius:50px}.header .price-bar-currency:focus{outline:none;outline-style:none;outline-style:initial}.header .price-bar-currency:hover{border-color:#cccccc}.header .price-bar{margin-right:20px;height:40px;width:150px;padding:2px 5px 7px 5px;display:flex;align-items:center;border:solid #e6e6e6 2px;border-radius:50px}.header .price-bar .search-field-price{width:100px;margin:0 0 0 5px;border:none;outline:none;font-size:1rem;background-color:transparent}.header .price-bar:hover{border-color:#cccccc}input[type="button"]{margin-bottom:20px}

.title{margin-top:50px;text-align:center;font-family:'Tajawal', sans-serif;font-size:30px;font-weight:bolder;align-content:flex-start}.hotel_container{display:flex;flex:0 0 20em;height:100px;margin-left:60px;margin-bottom:20px;overflow:hidden}.hotel_container img{width:30%;height:75px;margin-top:10px;border-radius:10%;overflow:hidden}.hotel_container__info_container{width:50%;margin-left:10px;padding-top:5px;line-height:1;font-size:15px;font-weight:bold;font-family:'Tajawal', sans-serif}.hotel_container__info_container .hotel_title{margin-top:5px;margin-bottom:0.5em;font-size:1rem}.sidebar{flex:0 0 25em}.sidebar .button-div{width:100%;display:flex;justify-content:center}.location{margin-top:0em;margin-bottom:0em;font-weight:normal}.price{width:40px;margin-top:1em;margin-bottom:0.1em;padding:5px 2px 5px 2px;border-radius:50px;text-align:center;background-color:#66c2ff;font-weight:bolder;font-size:12px;color:whitesmoke}.price:hover{background-color:#0099ff}.more{text-align:center;color:#9494b8;font-size:40px}.more:hover{color:#f0f0f5}.add-hotel-button{margin-top:20px;margin-bottom:20px;width:100px;text-align:center;border:2px solid #4db8ff;border-radius:5px;background-color:#4db8ff;color:whitesmoke;font-weight:normal}.closebutton:hover{border:2px solid #0099ff;background-color:#0099ff}

.add-container{margin:0 0 20px 60px;padding:20px;width:300px;border-radius:20px;text-align:center}.price-one-row{display:flex;justify-content:space-between}.form-control{border:2px solid #cccccc}.form-control:hover{border:2px solid #a6a6a6}.form-control:focus{outline:none}.buttons{display:flex;justify-content:space-around;width:100%}.add-hotel-button,.closebutton{margin-top:20px;text-align:center;width:100px;height:30px;border:2px solid #4db8ff;border-radius:5px;background-color:#4db8ff;color:whitesmoke}.add-hotel-button:hover,.closebutton:hover{border:2px solid #0099ff;background-color:#0099ff}.add-hotel-button:focus,.closebutton:focus{outline:none}

